// 
import request from '@/utils/request.js'

export function login(data) {
	return request({
		url: 'auth/login',
		method: 'POST',
		data
	})
}

export function getInfo(data) {
	return request({
		url: 'auth/getinfo',
		method: 'GET',
		data
	})
}

// putopenidstudents
export function editUserinfo(data) {
	return request({
		url: 'auth/editUserinfo',
		method: 'PUT',
		data
	})
} 